@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    text-decoration: none;
    color: #131C48;
    font-family: "Barlow", sans-serif;
    font-weight: 500;
    font-size: 18px;

    background-color: #E4E4E4;
}

a{
    color: #76E1E8;
    cursor: pointer;
}

button{
    display: inline-block;
    border-radius: 8px;
    border: none;
    width: 275px;
    height: 80px;
    padding: 10px;
    margin-bottom: 20px;
    background: #53C5CC;
    color: #ffffff;
    font-family: "Barlow", sans-serif;
    font-style: normal;
    font-size: 32px;
    line-height: 38px;
    text-align: center;
    cursor: pointer;
}
button:active, button:focus, input:active, input:focus{
    outline: none;
}
 
input{
    display: inline-block;
}
.App{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.container {
    padding: 85px 24px 0px;
    /* padding-top: 85px; */
    max-width: 1000px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.offer-list_item{
    display: block;
    max-width: 1200px;
    margin-bottom: 8px;
}

.offer{
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 200px;
    padding: 24px;
    background-color: #ffffff;
    border-radius: 8px;
}

.offer-header{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    width: 100%
}

.offer-header__button{
    display: block;
    margin-left: 24px;
    padding: 0px;
    width: 33px;
    height: 33px;
    background-color: #ffffff;
    border: none;
    color: #D6D7DD;
}

.offer-body{
    max-width: 940px;
    display: grid;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    grid-template-areas:"header"
                        "section-wrap"
                        "desc";
}

.offer-body__header-section{
    display: block;
    width: 100%;
    grid-area: header;
}

.offer-body__header-section--title{
    font-family: 'Bree Serif', serif;
    font-size: 44px;
    margin-bottom: 15px;
}

.offer-body__header-section--rating{
    display: inline-block;
    margin-bottom: 15px;
}

.offer-body__header-section-status{
    width: 250px;
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;

    border-radius: 8px;
    background-color: #53C5CC;
    font-size: 32px;
    color: #ffffff;

    margin-bottom: 25px;
    padding-bottom: 5px;

}
.offer-body__section-wrap{
    display: flex;
    flex-direction: column;
    width: 100%;
    grid-area: section-wrap;
}
.offer-body__location-section{
    display: flex;
    flex-direction: column;
    width: 100%;
    grid-area: location;
    text-align: left;

}
.offer-body__location-section > * {
    margin-bottom: 10px;
}

.offer-body__location-section--company{
    font-weight: 600;
    font-size: 24px;
    color: #2E365C;
}

/* .offer-body__location-section--paygrade{

} */

.offer-body__location-section--location > a{
    text-align: right;
}

.offer-body__link-section{
    display: block;
    width: 100%;
    grid-area: link;
    margin-bottom: 10px;
    font-size: 24px;
    text-align: end;
}

.offer-body__description-section{
    display: block;
    width: 100%;
    grid-area: desc;
}

.offer-body__description-section--description{
    text-align: left;
}
.filtered{
    display: none;
}

@media (min-width: 800px){
    .offer-body{
        width: 100%;
        padding: 0 82px 82px 82px;
        display: grid;
        grid-template-areas:"header section-wrap"
                            "desc section-wrap";
        grid-template-rows: auto 1fr;
        grid-template-columns: 70% 30%;
    }

    .offer-body__link-section{
        align-items: end;
        margin-bottom: 0px;
    }
    .offer-body__location-section{
        text-align: end;
    }
    .offer-body__section-wrap{
        justify-content: space-between;
    }
}
