.done{
    z-index: 10;
    width: 100vw;
    height: 100vh;
    position: fixed;
    background-color: #ffffff;

    display: flex;
    justify-content: center;
    align-items: center;

    opacity: 1;
    animation-name: fadeIn;
    -webkit-animation-name: fadeIn; 
    animation-duration: 0.2s;   
    -webkit-animation-duration: 0.2s;
    animation-timing-function: ease-in; 
    -webkit-animation-timing-function: ease-in;
}
.done--hide{
    display: none
}
.done-content{
    display: inline-block;
}
.done-animation{
    min-width: 40px;
    min-height: 40px;
}
.done-animation--visible{
    opacity: 1;
    animation-name: fadeIn;
    -webkit-animation-name: fadeIn; 
    animation-duration: 0.4s;   
    -webkit-animation-duration: 0.4s;
    animation-timing-function: ease-in; 
    -webkit-animation-timing-function: ease-in;
}
.done-text{
    font-family: 'Barlow', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 38px;
    text-align: center;

    color: #53C5CC;
}

@keyframes fadeIn {
    0% {
        opacity: 0.0;       
    }
    100% {
        opacity: 1; 
    }       
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0.0;       
    }
    100% {
        opacity: 1; 
    }      
}
